<template>
  <div class="row">
    <div class="col-md-12 mb-3">
      <h1>{{restaurant.name}}</h1>
      <p class="badge badge-secondary mt-1 mb-3">
        {{restaurant.categoryName}}
      </p>
    </div>
    <div class="col-lg-4">
      <img
        class="img-responsive center-block" 
        :src="restaurant.image | emptyImage"
        style="width: 250px;margin-bottom: 25px;"
      >
      <div class="contact-info-wrap">
        <ul class="list-unstyled">
          <li>
            <strong>Opening Hour:</strong>
            {{restaurant.openingHours}}
          </li>
          <li>
            <strong>Tel:</strong>
            {{restaurant.tel}}
          </li>
          <li>
            <strong>Address:</strong>
            {{restaurant.address}}
          </li>
        </ul>
      </div>
    </div>
    <div class="col-lg-8">
      <p>{{ restaurant.description ?  restaurant.description : "未描述"}}</p>
      <router-link
        class="btn btn-primary btn-border mr-2"
        :to="{ name: 'restaurant-dashboard', params: { id: restaurant.id } }"
      >
        Dashboard
      </router-link>

      <button
        v-if="restaurant.isFavorited"
        @click.stop.prevent="deleteFavorited(restaurant.id)"
        type="button"
        class="btn btn-danger btn-border mr-2"
      >
        移除最愛
      </button>
      <button
        v-else
        @click.stop.prevent="addFavorited(restaurant.id)"
        type="button"
        class="btn btn-primary btn-border mr-2"
      >
        加到最愛
      </button>
      <button
        v-if="restaurant.isLiked"
        @click.stop.prevent="deleteIsLiked(restaurant.id)"
        type="button"
        class="btn btn-danger like mr-2"
      >
        Unlike
      </button>
      <button
        v-else
        @click.stop.prevent="addIsLiked(restaurant.id)"
        type="button"
        class="btn btn-primary like mr-2"
      >
        Like
      </button>
    </div>
  </div>
</template>

<script>
import { emptyImageFilter } from '../utils/mixins'
import usersAPI from '../apis/users'
import { Toast } from '../utils/helpers'

export default {
  props: {
    initialRestaurant: {
      type: Object,
      require: true
    },
    restaurantComments: {
      type: Array,
      require: true
    }
  },
  data() {
    return {
      restaurant: this.initialRestaurant
    }
  },
  mixins: [ emptyImageFilter ],
  watch: {
    initialRestaurant(newValue) {
      this.restaurant = {
        ...this.restaurant,
        ...newValue
      }
    }
  },
  methods: {
    async addFavorited(restaurantID) {
      try {
        const { data } = await usersAPI.addFavorite({restaurantID} )
        
        if (data.status !== 'success') {
          throw new Error(data.methods)
        }
        this.restaurant = {
          ...this.restaurant,
          isFavorited: true
        }
      } catch (error) {
        Toast.fire({
          icon: 'error',
          title: '無法將餐廳加入到最愛，請稍候再試..'
        })
      }
    },
    async deleteFavorited(restaurantID) {
      try {
        const { data } = await usersAPI.deleteFavorite({ restaurantID })
        if (data.status !== 'success') {
          throw new Error(data.methods)
        }
        this.restaurant = {
          ...this.restaurant,
          isFavorited: false
        }
      } catch (error) {
        Toast.fire({
          icon: 'error',
          title: '無法將餐廳加入到最愛，請稍候再試..'
        })
      }
    },
    async addIsLiked(restaurantID) {
      try {
        const { data } = await usersAPI.addLiked({ restaurantID })
        if (data.status !== 'success') {
          throw new Error(data.methods)
        }
        this.restaurant = {
          ...this.restaurant,
          isLiked: true
        }
      } catch(error) {
        Toast.fire({
          icon: 'error',
          title: '無法對餐廳按讚，請稍後再試..'
        })
      }
    },
    async deleteIsLiked(restaurantID) {
      try {
        const { data } = await usersAPI.deleteLike({ restaurantID })
        if (data.status !== 'success') {
          throw new Error(data.methods)
        }
        this.restaurant = {
          ...this.restaurant,
          isLiked: false
        }
      } catch(error) {
        Toast.fire({
          icon: 'error',
          title: '無法對取消讚，請稍後再試..'
        })
      }
    }
  }
}
</script>

<style scoped>
.col-lg-8 p,
.contact-info-wrap li,
.contact-info-wrap strong {
  font-family: serif;
  font-size: 17px;
}
</style>